import React from "react";
import PropTypes from "prop-types";


const WinScreen = (props) => {
  const {questionsCount, mistakesCount, onReplayButtonClick} = props;
  const correctlyQuestionsCount = questionsCount - mistakesCount;

  return (
    <section className="result">
      <div className="result__logo">
        <img src="img/melody-logo.png" alt=" " width="186" height="83" />
      </div>
      <h2 className="result__title">  !</h2>
      <p className="result__total">    {correctlyQuestionsCount}    {mistakesCount} </p>
      <button
        className="replay"
        type="button"
        onClick={onReplayButtonClick}
      >
          
      </button>
    </section>
  );
};

WinScreen.propTypes = {
  questionsCount: PropTypes.number.isRequired,
  mistakesCount: PropTypes.number.isRequired,
  onReplayButtonClick: PropTypes.func.isRequired,
};


export default WinScreen;
